        { Estudiante: 'Ricardo Vargas Farfan', Etnia: 'shipibo', Escuela: 'Educacin Inicial Bilingue' },
        { Estudiante: 'Jose Olaya de la Vega', Etnia: 'shipibo', Escuela: 'Educacin Inicial Bilingue' },
